import { _decorator, Component, Node, Vec2, Intersection2D } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Polygon')
export class Polygon extends Component {
    
    public poly:Vec2[]=[];
    public pname:string="";
    public hint:string="";

    start() {

    }
    update(deltaTime: number) {
        
    }
    
    hitInPolygon(pt:Vec2)
    {
        if(Intersection2D.pointInPolygon(pt,this.poly))
        {
            return true;
        }
        return false;
    }
}

